export default {
  data() {
    return {
      topTitle: "身份认证",
      showPositionValue: false, //提示信息
      toastContent: "",
      errorConten: "",
      list: {
        idcard: "",
        user_name: ""
      }
    };
  },

  methods: {
    getIdCard() {
      var that = this,
        formData,
        reg = /^[\u4E00-\u9FA5]{2,4}$/,
        regs = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
      if (that.list.user_name == "" || that.list.idcard == "") {
        // that.showPositionValue = true;
        // that.toastContent = "姓名/身份证号码不能为空";
        that.errorConten = "姓名/身份证号码不能为空";
      } else if (!reg.test(that.list.user_name)) {
        // that.showPositionValue = true;
        // that.toastContent = "姓名只能是汉字并且在2-4位之间";
        that.errorConten = "姓名只能是汉字并且在2-4位之间";
      } else if (!regs.test(that.list.idcard)) {
        // that.showPositionValue = true;
        // that.toastContent = "请输入正确的身份证号码";
        that.errorConten = "请输入正确的身份证号码";
      } else {
        formData = {
          sign: that.$store.state.user.sign,
          access_token: that.$store.state.user.token,
          idcard: that.list.idcard,
          user_name: that.list.user_name
        };
        that
          .$request({
            method: "POST",
            url: that.$baseURL + "/personal/bind-user",
            data: that.$qs.stringify(formData)
          })
          .then(function(response) {
            if (response.code == 0) {
              that.showPositionValue = true;
              that.toastContent = response.errmsg;
              that.errorConten = response.errmsg;
              that.$router.push("/myDetails");
            } else {
              that.showPositionValue = true;
              that.toastContent = response.errmsg;
              that.errorConten = response.errmsg;
            }
          })
          .catch(function(error) {
            console.log(error);
          });
      }
    }
  }
};



